import { Block, Controller } from '@ali/4ever-cangjie';
import {
  findPrevSubjectIndex as helpers_findPrevSubjectIndex,
  findNextSubjectIndex as helpers_findNextSubjectIndex,
} from '../helpers';

export function findPrevSubjectIndex(controller: Controller, from?: Block): number {
  return helpers_findPrevSubjectIndex(controller.value, from);
}

export function findNextSubjectIndex(controller: Controller, from?: Block): number {
  return helpers_findNextSubjectIndex(controller.value, from);
}
